import StorageOutlined from "@mui/icons-material/StorageOutlined";
import { Box, Card, CardContent, Container, Divider, Grid, Typography } from "@mui/material";
import { useSearchParams } from "react-router-dom";
import { useMiddleware, useTask } from "../../../../Application";
import { TabView } from "../../../../tabs";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { Cell, Pie, PieChart, ResponsiveContainer as RechartResponsiveContainer } from "recharts";
import { useEffect, useState } from "react";
import formatBytes from "../../../../utils/formatBytes";
import FolderCopyOutlined from "@mui/icons-material/FolderCopyOutlined";


const ManageStorage = () => {

    const [searchParams, setSearchParams] = useSearchParams();

    const middleware = useMiddleware('Core.Workspace');

    const [quotes, setQuotes] = useState(null);

    useEffect(() => {

        const loadQuotesAsync = async () => {
            const response = await middleware.quotes();

            const { capacity, usedSpace, freeSpace } = response.data;

            setQuotes({
                capacity,
                usedSpace,
                freeSpace
            });
        }

        loadQuotesAsync();
    }, []);

    const task = useTask('Storage.manageStorage');
    

    return (
        <TabView {...task}>
            <ResponsiveContainer>
                <Container disableGutters>
                    <Grid container justifyContent="center">
                        <Grid item xs={12} sm={6} md={8}>
                            <Card variant="outlined">
                                <CardContent sx={{ display: "flex" }}>
                                    <Box>
                                        <RechartResponsiveContainer width={200} height={200}>
                                            <PieChart>
                                                <Pie
                                                    data={[{
                                                        name: "مصرف شده",
                                                        value: quotes && quotes['usedSpace']
                                                    }, {
                                                        name: "باقیمانده",
                                                        value: quotes && quotes['freeSpace']
                                                    }]}
                                                    dataKey="value"
                                                    nameKey="name"
                                                    // cx="50%"
                                                    // cy="50%"
                                                    innerRadius={30}
                                                    // outerRadius={80}
                                                    labelLine={false}
                                                    label={false}
                                                >
                                                    <Cell fill="#0067a5" />
                                                    <Cell fill="rgb(218, 220, 224)" />
                                                </Pie>
                                            </PieChart>
                                        </RechartResponsiveContainer>
                                    </Box>
                                    <Box sx={{ flexGrow: 1 }}>
                                        <Typography variant="h5" sx={{ mb: 1, mt: 5 }}>
                                            فضای ذخیره‌سازی
                                        </Typography>
                                        <Typography variant="h5" fontWeight={900} display="inline-block">
                                            <span dir="ltr">{quotes && formatBytes(quotes['usedSpace'])}</span>
                                        </Typography>
                                        {" "}
                                        <Typography variant="subtitle1" display="inline-block">
                                            از <span dir="ltr">{quotes && formatBytes(quotes['capacity'])}</span> استفاده شده
                                        </Typography>

                                        <Divider sx={{ my: 2 }} />

                                        <Typography variant="body1">فضای ذخیره‌سازی شامل موارد زیر می‌باشد:</Typography>

                                        <Box sx={{ display: "flex", mt: 2 }} >
                                            <Box sx={{ mr: 2 }}>
                                                <FolderCopyOutlined fontSize="large" />
                                            </Box>
                                            <Box sx={{ flexGrow: 1 }}>
                                                <Typography fontWeight={700}>فایل‌های آپلود شده</Typography>
                                                <Typography>تمامی فایل‌ها، تصاویر و رسانه‌های آپلود شده توسط کاربران مختلف در برنامه‌های مختلف در فضای‌کار</Typography>
                                            </Box>
                                        </Box>


                                        <Box sx={{ display: "flex", mt: 2 }} >
                                            <Box sx={{ mr: 2 }}>
                                                <StorageOutlined fontSize="large" />
                                            </Box>
                                            <Box sx={{ flexGrow: 1 }}>
                                                <Typography fontWeight={700}>هاستینگ و برنامه‌ها</Typography>
                                                <Typography>تمام فایل‌های موردنیاز برای اجرای برنامه‌های مختلف به صورت عمومی، مثل وبسایت، اپلیکیشن و گواهی امنیتی و...</Typography>
                                            </Box>
                                        </Box>
                                    </Box>
                                </CardContent>
                            </Card>
                        </Grid>
                    </Grid>
                </Container>
            </ResponsiveContainer>
        </TabView>
    )
}

export default ManageStorage;